.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar .navbar-brand img {
  width: 40px;
}

.countdown-container {
  padding-top: 8%;
  background-color: black;
  height: calc(100vh - 66px);
}

.text-center {
  color: white;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 1px solid #fff;
  margin-right: 10px;
}

.coming-soon {
  text-align: center;
  color: #fff;
  font-family: 'Moontime', sans-serif;
  font-size: 125px;
}

.days, .hours, .minutes, .seconds {
  float: left;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  font-family: 'League Spartan', arial;
  font-size: 25px;
  padding-top: 32px;
}

.logo-container {
  text-align: center;
}

.container-fluid .logo-container .col img {
  width: 150px;
}
